.counter__wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background: rgb(51, 51, 51);
  background: linear-gradient(97deg, rgba(51, 51, 51, 1) 0%, rgba(221, 24, 24, 1) 100%);
}

.counter {
  width: 600px;
  padding: 50px;
  border-radius: 10px;
  background: rgb(237, 33, 58);
  background: radial-gradient(circle, rgba(237, 33, 58, 1) 0%, rgba(147, 41, 30, 1) 100%);
  color: black;
  box-shadow: 10px 10px 10px 2px rgba(34, 60, 80, 0.2) inset;
}

.panel__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin: 10px;
  border-radius: 10px;
  font-size: 200px;
  font-weight: 700;
  background: #eeeeee;
  color: black;
}

.panel__wrapperError {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin: 10px;
  border-radius: 10px;
  font-size: 200px;
  font-weight: 700;
  color: #FFFFFF;
  background: #000;
  text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
  color: #FFFFFF;
  background: #333333;
}

.buttons__wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
  padding: 20px;
  margin: 10px;
  background: #eeeeee;
  border-radius: 10px;
}

.button {
  width: 200px;
  margin: 10px;
  padding: 15px 30px;
  text-align: center;
  text-transform: uppercase;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border: none;
  border-radius: 10px;
  display: block;
  background-image: linear-gradient(to right, #232526 0%, #414345 51%, #232526 100%);
  letter-spacing: 3px;
  font-size: 30px;
  cursor: pointer;
  outline: none;
  user-select: none;
  transition: background-color 250ms ease-in-out,
  transform 50ms ease-in-out;

  &:disabled {
    opacity: 0.5;
  }

  &:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none;
  }

  &:active {
    transform: scale(0.9);
  }
}

.button__disable {
  opacity: 0.5;
}





